<template>
  <v-container fluid>
    <v-row>
      <v-col>
        <v-textarea
            label="Входной текст"
            solo
            v-model="inputText"
        ></v-textarea>
        <v-btn
            @click="transformText"
        >Преобразовать
        </v-btn>
      </v-col>
      <v-col>
        <v-card
            min-height="150"
          v-scroll
        >
          <v-card-text
              v-html="outputText"
              style="font-size: 16px"
          >
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

import axios from "axios";
import {TRANSFORM_PATH} from "@/constants/api";

export default {
  name: "Main",
  data() {
    return {
      inputText: "",
      outputText: "Выходной текст",
      clusterText: "",
      testText: "",
      replacements: []
    }
  },
  methods: {
    async transformText() {
      let payload = {
        inputText: this.inputText
      }
      const data = (await axios.post(TRANSFORM_PATH, payload)).data;
      this.testText = data.outputText;
      let replacements = data.replacements.map((el) => {
        el.start = parseInt(el.start, 10);
        el.end = parseInt(el.end, 10);
        return el;
      });
      this.replaceText(data.inputText, replacements)

    },

    replaceText(text, replacements) {
      replacements = replacements.sort((a, b) => {
        return a.start - b.start
      })

      let outputText = "";
      let startPos = 0;
      let endPos = 0;
      for (const replacement of replacements) {
        endPos = replacement.start - 1;
        outputText = outputText
            + text.slice(startPos, endPos)
            + `<span class="amber--text" title="${replacement.tokenold}">` + replacement.tokennew + '</span>'
        startPos = replacement.end;
      }
      endPos = text.length
      outputText = outputText + text.slice(startPos, endPos);
      this.outputText = outputText;


    }

  }
}
</script>

